<template>
  <div>
    <div class="Conference"> </div>
    <div class="ChatList">
      <div id="app">
        <div v-for="(item, idx) in recvList" :key="idx">
          <h3>: {{ userInfo.userName }}</h3>
          <h3>: {{ item.content }}</h3>
        </div>
      </div>
    </div>
    <div class="ToolBox"> </div>
    <div class="ChatBox">
      {{ userInfo.user_name }} :
      <input v-model="message" type="text" @keyup="sendMessage" />
    </div>
    <hr>
    <div> <!--     () -->
      <p>  </p>
      <p>conference name: {{ conferenceDetail.title }}
      / conference type: {{ categoryName }}</p>
    </div>
    <hr>
    <div> <!--   /   --> <!--  ()   -->
      <conference-detail-update></conference-detail-update>
      <conference-detail-close></conference-detail-close>
    </div>
  </div>
</template>

<script>

import { mapState, mapActions } from "vuex";
import ConferenceDetailUpdate from './ConferenceDetailUpdate';
import ConferenceDetailClose from './ConferenceDetailClose';

const userStore = "userStore";
const conferenceStore = "conferenceStore";

export default {
  name: "Conference_detail",
  components: {
    ConferenceDetailUpdate,
    ConferenceDetailClose
  },
  data() {
    return {
      userName: "",
      message: "",
      recvList: [],
      //  
      categoryName: "",
      //   
      conferenceId: this.$route.params.contentId
    };
  },
  created() {
    // App.vue    .
    // this.connect();
    ///////////////////////////////////////////////

    //    

    this.getConferenceInfo(this.conferenceId);

    //   
    this.getConferenceCategories();
    //     
    this.matchCategory();

    
    //    
    // this.createParticipant(this.createData());

    //      
    // this.createHistory(this.historyData());
  },
  watch: {
    conferenceDetail(){
      this.matchCategory();
    } 
  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
    ...mapState(conferenceStore, ["conferenceDetail", "conferenceCategory"]),
  },
  methods: {
    ...mapActions(conferenceStore, ["getConferenceInfo", "getConferenceCategories", "createHistory", "createParticipant"]),
    //     
    matchCategory() {
      for (const cc of this.conferenceCategory)
        if (cc.id === this.conferenceDetail.conference_type) {
          this.categoryName = cc.name
        }
    },
  },
};
</script>

<style>
.Conference {
  border: solid;
  float: left;
  width: 70%;
  height: 800px;
}
.ChatList {
  border: solid;
  float: right;
  width: 30%;
  height: 800px;
}
.ToolBox {
  border: solid;
  float: left;
  width: 70%;
  height: 200px;
}
.ChatBox {
  border: solid;
  float: right;
  width: 30%;
  height: 200px;
}
</style>
