<template>
  <div>
    <b-button 
    v-b-modal.modal3-prevent-closing 
    variant="primary" 
    class="col-4 mb-3"
    @click="$bvModal.show('modal-scoped')"
    > </b-button>
      <b-modal
      id="modal3-prevent-closing"
      ref="modal"
      title=" "
    >
      <form ref="form">
        <b-form-group
          class="roommodal"
          id="dropdown-1"
          label=""
          label-for="usage-roominput"
        >
          <b-form-select v-model="selectedOption" :options="options">
            <template #first>
              <b-form-select-option :value="null" disabled>-- --</b-form-select-option>
            </template>
          </b-form-select>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          class="roommodal"
          label=" "
          label-for="name-roominput"
        >
          <b-form-input
            id="name-input"
            v-model="roomName"
            placeholder="  "
          ></b-form-input>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          type="textarea"
          class="roommodal"
          label=""
          label-for="content-roominput"
        >
          <b-form-textarea
            id="textarea"
            v-model="roomContent"
            placeholder=" "
            row="3"
            max-rows="6"
          ></b-form-textarea>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          type="file"
          class="roommodal"
          label=""
          label-for="thumbnail-roominput"
        >
          <b-form-file
            accept=".png, .jpg, .jpeg, .gif"
            v-model="thumbnailFile"
            class="mt-3"
            plain
          ></b-form-file>
          <!-- <div class="mt-3">Selected file: {{ thumbnailFile ? thumbnailFile.name : "" }}</div> -->
        </b-form-group>
      </form>
      <br />
      <template #modal-footer="{ ok, cancel }">
        <b-button variant="outline-primary" class="me-2" @click="roomCreate() ? ok() : _">  </b-button>
        <b-button @click="formReset(); cancel();">  </b-button> 
      </template>

      
    </b-modal>

  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Swal from "sweetalert2";

export default {
  name: "ConferenceCreate",
  data() {
    return {
      roomName: "",
      roomContent: "", 
      thumbnailFile: "",
      selectedOption: "1", // ( )    
      options: [
        { value: '1', text: '' },
        { value: '2', text: '' },
        { value: '3', text: '' },
      ],    

    };
  },
  created() {

  },
  computed: {
    ...mapState("userStore", ["userInfo"]),
  },
  methods: {
    ...mapActions("conferenceStore", ["createRoom"]),
    roomIsValid: function () {
      console.log(this.thumbnailFile) // thumbnail    
      if (
        this.selectedOption === null ||
        this.roomName === ""
      ) {
        Swal.fire({
          icon: "error",
          title: "Stop!",
          text: ",   .",
        });
        return false
      } else if (this.roomName.length > 31) {
        Swal.fire({
          icon: "error",
          title: "RoomnameError",
          text: "  30  .",
        });
        return false
      } else if (this.thumbnailFile.size > 2097152) { //size byte 
        Swal.fire({
          icon: "error",
          title: "ImgsizeError",
          text: "     2MB.",
        });
        return false
      } else {
        console.log(this.selectedOption);
        return true
      }
    },
    roomData() {
      return {
        conference_type: this.selectedOption,
        book_detail_id: 1, //  - book DB  
        // call_start_time: this.date_time(), //  
        user_user_id: this.userInfo.user_id,
        thumbnail_url: this.thummbnailFile, //   -   
        title: this.roomName,
        //  ??  ???   
        is_active: 1, //  0() 1() 
        participant: null
      }
    },
    formReset() {
        this.selectedOption = null
        this.thumbnailFile = ""
        this.roomName = ""
        this.roomContent = "" //   //  -  DB     :  
    }, //  form 
    roomCreate() {
      if (this.roomIsValid()) {
        this.createRoom(this.roomData()) //   //   payload 
        this.formReset() //  form 
        return true
      }
      else {
        return false
      }
    },
  },
};
</script>

